
.card{
    display: flex;
    flex-direction: column;
    margin-left: 50px;
    justify-content: center;
    justify-items: center;
}


.swim{
    height: 250px;
    width: 200px;
    object-fit: cover;
    border-radius: 15px;
}
.badge{
    border-style:ridge;
    text-align: center;
    color: rgb(0, 0, 0);
    display: inline;
}

.star{
    width: 15px;
}
.cardinfo{
    text-align: center;
    display: flex;
    gap: 10px;
}
.tittle{
    margin-top: -1px;
}
.price{
    margin-top: -10px;
}
.tag{
    font-weight: bold;
}